import { KEY12_SHARP_LIST } from "contents/common/define"
import { GlobalContext } from 'contents/MainFrame';
import { useContext, useEffect, useState } from 'react';
import './SamplerTab.css';

type SampleRecord = {
    // 音名
    soundName: string,
    // レコードの利用有無
    isUse: boolean,
    // URL
    url: string
};

const SamplerTab = () => {
    const { state, dispatcher } = useContext(GlobalContext);

    const [samples, setSamples] = useState<SampleRecord[]>();

    useEffect(() => {

        const samples: SampleRecord[] = [];

        for (let i = 0; i <= 88; i++) {
            samples.push(
                {
                    soundName: KEY12_SHARP_LIST[(i + 9) % 12] + Math.floor((i + 9) / 12),
                    isUse: false,
                    url: ""
                },
            );
        }
        setSamples(samples);
    }, []);

    const createRecored = (): JSX.Element => {
        const list: JSX.Element[] = [];
        for (let i = 0; i < 88; i++) {
            if (samples && samples[i]) {
                list.push(
                    <div className="record">
                        <div className="label"><span>{samples[i].soundName}</span></div>
                        <input type="checkbox"
                            checked={samples[i].isUse}
                            onChange={isuse => {
                                samples[i].isUse = isuse.target.checked;
                                setSamples(samples.slice());
                            }}
                        />
                        <input
                            type="text"
                            className={!samples[i].isUse ? 'disable' : ''}
                            value={samples[i].url}
                            onChange={url => {
                                samples[i].url = url.target.value;
                                setSamples(samples.slice());
                            }} />
                    </div>
                );
            }
        }
        return (
            <>{list}</>
        );
    }

    const getSamplesList = () => {
        let samplesList = {};
        for (let i = 0; i < 88; i++) {
            if (samples && samples[i]) {
                if (samples[i].isUse) {
                    samplesList = { [samples[i].soundName]: samples[i].url };
                }
            }
        }
        return samplesList;
    }

    return (
        <div id="samplertab-root">
            // サンプラーの読み込みdiv
            <div className="loader"></div>

            // 音程リストdiv
            <div className="list">
                {createRecored()}
            </div>

            // 制御ボタンdiv
            <div className="operation">
                <button
                    className={[
                        'disable'
                    ].join(' ')}
                    onClick={() => {

                    }}>Save</button>

                <button
                    className={[
                        'disable'
                    ].join(' ')}
                    onClick={() => {

                    }}>Reset</button>

                <button onClick={() => {
                    dispatcher.setSampler(getSamplesList());
                }}>Build</button>
            </div>
            <div className="keyboad"></div>
        </div>
    );
}

export default SamplerTab;